<template>
	<view class="">
		<!-- 顶部信息 -->
		<view class="HomeTop">
			<view class="logoView">
				<!-- logo -->
				<img class="logo" src="/static/index/logo.jpg" alt="" />
			</view>
			<view class="inputView">
				<input class="textinput" style="center" type="text" v-model="keyword" placeholder="寻找商品" @blur="toGoodsList"/>
			</view>
		</view>

		<!-- 顶部导航  scroll-view -->
		<view class="tabs">
			<scroll-view id="tab-bar" class="scroll-h" :scroll-x="true" :show-scrollbar="false">
				<view class="uni-tab-item" data-current="0">
					
					<text class="uni-tab-item-title uni-tab-item-title-active">
						推荐
					</text>
				</view>	
				<view class="uni-tab-item" data-current="0" v-for="item in menuList" key="item.id">
					
					<text class="uni-tab-item-title">
						{{item.catename}}
					</text>
				</view>	
			</scroll-view>
		</view>
		<!-- 推荐轮播图 -->
		<view class="">
			<swiper :indicator-dots="true" :autoplay="true" :interval="2000" :duration="1000">
				<swiper-item v-for="item in bannerList" key="item.id">
					<view class="swiper-item">
						<image :src="item.img" mode=""></image>
					</view>
				</swiper-item>



			</swiper>
		</view>

		<!-- 功能导航开始 -->
		<!-- 功能导航 -->
		<view class="FunctNavCon">
			<view class="FunctNavLi">
				<image src="../../static/index/xiaohuoban.png" alt class="FunImg" />
				<p>限时抢购</p>
			</view>
			<view class="FunctNavLi">
				<image src="../../static/index/jifentixicopy.png" alt class="FunImg" />
				<p>积分商城</p>
			</view>
			<view class="FunctNavLi">
				<image src="../../static/index/lianxiwomen.png" alt class="FunImg" />
				<p>联系我们</p>
			</view>
			<view class="FunctNavLi" @click="toclassify">
				<image src="../../static/index/-shangpinfenlei-gai.png" alt class="FunImg" />
				<p>商品分类</p>
			</view>
		</view>

		<!-- 推荐部分 未写 -->
		<view class="HotGoods">
			<!-- 热推左侧 -->
			<view class="HotLeft">
				<view class="LimitedContent">
					<image class="LimitedImg" src="../../static/index/xianshi.jpg" alt />
					<label class="Limited">限时秒杀</label>
				</view>
				<p class="LimitedTitle">每天零点场，好货秒不停</p>
				<!-- 倒计时 -->
				<view class="LimitTimeAll">
					<label class="LimitTime">{{h}}</label>
					<view class="maohao">:</view>
					<label class="LimitTime">{{m}}</label>
					<view class="maohao">:</view>
					<label class="LimitTime">{{s}}</label>
					<label class="Seckill">秒杀</label>
				</view>
				<image src="../../static/index/goods2.jpg" class="LimitedgoodsImg" alt="">
			</view>

			<!-- 热推右侧 -->
			<view class="HotRight">
				<!-- 右侧顶部部分 -->
				<view class="HotTop">
					<view class="HomeTopInfo">
						<view class="HomeTopInfoOne">
							<label class="BrandNew">品牌上新</label>
							<label class="Discount">折</label>
						</view>
						<view class="HomeTopRobbig">
							<label>每日九点，抢大牌</label>
						</view>
					</view>
					<!-- <div> -->
					<view class="Cloth">
						<image src="../../static/index/goods1.jpg" alt="">
					</view>

					<!-- </div> -->
				</view>
				<!-- 右侧底部 -->
				<view class="HotBottom">
					<view class="HotBottomLeft">
						<view class="HotBottomLeftInfo">
							<label class="HotBottomLeftInfoTitle">每日十件</label>
							<label class="HotBottomLeftInfoDetail">只为你选好货</label>
						</view>
						<view class="HotBottomLeftImage">
							<image src="../../static/index/goods1.jpg" mode=""></image>
						</view>
					</view>
					<view class="HotBottomRight">
						<view class="HotBottomRightInfo">
							<label class="HotBottomRightInfoTitle">每日十件</label>
							<label class="HotBottomRightInfoDetail">只为你选好货</label>
						</view>
						<view class="HotBottomRightImage">
							<image src="../../static/index/goods1.jpg" mode=""></image>
						</view>
					</view>
				</view>
			</view>
		</view>
		<!-- 轮播图2 -->
		<div class="Swiper2">
			<image class="Swiper2Img" src="../../static/index/swiper1.jpg" alt />
		</div>

		<!-- 底部商品列表 -->
		<view class="products">
			
			<!-- 商品标签 -->
			<view class="tags">
				<view :class="[navActive==0?'tag_list active_tag_list':'tag_list']"  @click="nav(0)">
					<label for="">热门推荐</label>
				</view>
				<view :class="[navActive==1?'tag_list active_tag_list':'tag_list']" @click="nav(1)">
					<label for="">发现好货</label>
				</view>
				<view :class="[navActive==2?'tag_list active_tag_list':'tag_list']" @click="nav(2)">
					<label for="">只看专场</label>
				</view>
			</view>
			<!-- 标签对应的商品 -->
			<view class="tags_product">
				<view class="product" v-for="(item,index) in goodsList" :key="item.id">
					<view class="GoodsLeft">
						<image class="GoodsImg" :src="item.img" alt />
					</view>
					<view class="GoodsCont">
						<view class="GoodConTit">{{item.goodsname}}</view>
						<view class="GoodsPrice">￥{{item.price}}</view>
						<view class="yimai">已售8000件</view>
						<view class="Immed" @click="toDetail(item.id)">立即抢购</view>
					</view>
				</view>

			</view>
		</view>
	</view>
</template>

<script>
	import {
		getcate,
		getbanner,
		getseckill,
		getindexgoods,
	} from "../../utils/api.js"
	import {
		baseUrl
	} from "../../utils/tool.js"

	export default {
		data() {
			return {
				menuList: [],
				bannerList: [],
				h: 0,
				m: 0,
				s: 0,
				navActive:0,
				result:null,
				goodsList:[],
				keyword:''
				
			}

		},
		
		methods:{
			
			
			
			nav(x){
				this.navActive = x
				
				switch(x){
				 case 0 : this.goodsList = this.result.data.list[0].content; break;
				 case 1 : this.goodsList = this.result.data.list[1].content; break;
				 case 2 : this.goodsList = this.result.data.list[2].content; break;
							   
							   
				}
				
			
				
			},
			
			toGoodsList(){
				var keyword = this.keyword
				console.log(keyword)
				
				uni.navigateTo({
				    url: '/pages/search/search?keyword='+keyword
				});
				
				
			},
			
			
			toclassify(){
				
				uni.navigateTo({
				    url: '/pages/classify/classify',
				});
			},
			
			toDetail(id){
				console.log(id)
				uni.navigateTo({
				    url: '/pages/details/details?id='+id,
				});
			}
			
			
			
			
		   
		},
		
		

		async onLoad() {



			var time = setInterval(() => {
				var nowtime = new Date(); //获取现在时间
				var endtime = new Date("2020/10/01,00:00:00"); //获取将来时间
				var lefttime = parseInt((endtime.getTime() - nowtime.getTime()) / 1000); //两个时间的毫秒数相减
				//毫秒数转化为时间格式
				var d = parseInt(lefttime / 3600 / 24);
				var h = parseInt((lefttime / 3600) % 24);
				var m = parseInt((lefttime / 60) % 60);
				var s = parseInt(lefttime % 60);
                if(h<10){
					h = "0"+h
				}
				
				if(m<10){
					m = "0"+m
				}
				
				if(s<10){
					s = "0"+s
				}
				
				this.h = h
				this.m = m
				this.s = s
			}, 1000)
               


			var result = await getcate();
			this.menuList = result.data.list
            console.log(result)

			var result1 = await getbanner();

			var arr = result1.data.list
			arr.forEach(item => {
				item.img = baseUrl + item.img
			})
			
			this.bannerList = arr

			var result2 = await getseckill();
			
			var result3 = await getindexgoods();
			
			result3.data.list.forEach(item => {
				item.content.forEach(item1=>{
					item1.img = baseUrl + item1.img
				})
				
			})
			console.log(result3)
			this.result = result3
			this.goodsList = result3.data.list[0].content
            
			

		}

	}
</script>

<style>
	/* 导入外部的样式文件 */
	@import url("../../common/css/index.css");
</style>
